<template>
  <div ref="sponsors" id="sponsors">
    <Heading :subHeading="'Previous'" anchor=".sponsors" wrapper="#sponsors" :speed="-6">Sponsors</Heading>
    <div class="sponsors" id="sponsors-list">
      <div
        v-for="(sponsorLayer, i) in sponsors"
        :key="i"
        class="absoulte"
        :class="i"
        v-rellax="i == 'layer-1' ? layer1 : i == 'layer-2' ? layer2 : layer3"
      >
        <div class="container-fluid">
          <div class="row justify-content-around" v-for="(sponsorRow, j) in sponsorLayer" :key="j">
            <div
              class="sponsor-item"
              v-for="(sponsor, k) in sponsorRow"
              :key="k"
              :style="{ 'background-image': 'url(' + getImgUrl(sponsor.image) + ')'}"
              v-on:click="/*openLink(sponsor.url)*/"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- <p class="text-white" v-for="(sponsor,i) in sponsors" :key="i">{{ sponsor }}</p> -->
    <!-- <div class"sponsor-item" @click="getImgUrl(sponsor.url)"></div> -->
  </div>
</template>

<script>
import sponsors from "@/assets/sponsors/sponsors.json";



export default {
  data() {
    return {
      sponsors: sponsors,
      layer1: {
        speed: 1,
        wrapper: "#sponsors",
        relativeToWrapper: true
      },
      layer2: {
        speed: -0.7,
        wrapper: "#sponsors",
        relativeToWrapper: true
      },
      layer3: {
        speed: 1.2,
        wrapper: "#sponsors",
        relativeToWrapper: true
      }
    };
  },
  mounted() {
    this.resizeHandler();
    window.addEventListener("resize", this.resizeHandler);
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeHandler);
  },
  methods: {
    openLink(link){
      if(link == undefined || link == "") return;
      console.log("LINK : " + link);
      window.open(link, '_blank');
    },
    resizeHandler(e) {
      console.log("event");
      let width = document.body.clientWidth;
      if (width <= 768) {
        this.layer1 = false;
        this.layer2 = false;
        this.layer3 = false;
        this.layer4 = false;
        this.layer1 = {
          speed: 0,
          wrapper: "#sponsors",
          relativeToWrapper: true
        };
        this.layer2 = {
          speed: 0,
          wrapper: "#sponsors",
          relativeToWrapper: true
        };
        this.layer3 = {
          speed: 0,
          wrapper: "#sponsors",
          relativeToWrapper: true
        };

        this.layer4 = {
          speed: 0,
          wrapper: "#sponsors",
          relativeToWrapper: true
        };
      }
    },
    getImgUrl(img) {
      return require("../assets/sponsors/" + img);
    }
  }
};
</script>

